import React from 'react'
import {List, Cell, PanelHeader, platform, ANDROID, Header, Div, Group, Gallery} from '@vkontakte/vkui'
import '@vkontakte/vkui/dist/vkui.css'


class Tasks extends React.Component {
	render() {

        let {
            tasks,
            router,
            setCurrentTaskId,
            deleteTask,
            removable
        } = this.props

        const osname = platform()

		return (
			<div>
                <PanelHeader

                >
                    Тесты
                </PanelHeader>
                <List style={{ paddingTop : (osname === ANDROID) ? 56 : 48 }}>
                    {
                        tasks.map((task, index) => (
                            <Cell
                                multiline
                                expandable
                                removable={removable}
                                key={index}
                                onRemove={() => deleteTask(task.id)}
                                onClick={()=> {
                                        setCurrentTaskId(task.id)
                                        router.navigate('task', { id : task.id })
                                    } 
                                }
                            >
                                <Header>{task.name}</Header>
                                <Gallery
                                    slideWidth="90%"
                                    style={{ height: 150 }}
                                >
                                    <div style={{ backgroundColor: 'var(--accent)' }} />
                                </Gallery>
                                <Div>{task.text}</Div>

                            </Cell>
                        ))
                    }
                </List>
            </div>
		);
	}
}

export default Tasks;
